header {
    padding: 1.5rem 0;

    .wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &.inner {
        box-shadow: 0px 61px 17px 0px rgba(173, 173, 173, 0.00), 0px 39px 16px 0px rgba(173, 173, 173, 0.01), 0px 22px 13px 0px rgba(173, 173, 173, 0.05), 0px 10px 10px 0px rgba(173, 173, 173, 0.09), 0px 2px 5px 0px rgba(173, 173, 173, 0.10);
    }

    .menu-icon {
        display: none;

        svg {
            path {
                fill: $primary;
            }
        }

        @media (max-width: 991.98px) {
            display: inline-flex;



        }
    }

    .menu {
        ul {
            display: flex;
            align-items: center;
            padding: 0;
            margin-bottom: 0;

            li {
                margin: 0 10px;
                font-size: 1.1rem;

                a {
                    color: #000;
                    font-family: $family3;
                    position: relative;



                    &:after {
                        content: "";
                        position: absolute;
                        bottom: -2px;
                        left: 0;
                        height: 2.5px;
                        width: 100%;
                        background: transparent;

                    }

                    &:hover {
                        color: $primary;
                        font-weight: bold;

                        &:after {
                            background: $primary;
                        }
                    }
                }
            }
        }

        .close-menu {
            display: none;

            svg {
                cursor: pointer;
            }
        }

        @media (max-width: 991.98px) {
            flex-direction: column;
            position: fixed;
            top: 0;
            left: -300px;
            width: 300px;
            height: 100vh;
            background-color: #fff;
            transition: all 2s;
            z-index: 100;

            &.active {
                left: 0;
                z-index: 100;
            }

            .close-menu {
                display: block;
            }

            ul {
                height: 100vh;
                padding: 100px 50px;
                flex-direction: column;


                li {
                    padding: 1rem 0;

                    a {
                        font-family: $family;

                    }
                }

            }
        }

    }



    .sub-lang {
        display: flex;
        align-items: center;
        justify-content: left;

        .lang-ico {
            width: 20px;
            height: 20px;
            line-height: 20px;
            text-align: center;
            background: #2E8B39;
            display: inline-block;
            color: #fff;
            @include border-radius(50%);
            font-family: $family2;
            font-size: .8rem;
        }


        .btn-join {
            padding: 0;
            width: 122px;
            height: 46px;
            background-color: $primary;
            color: #fff;
            @include border-radius(24px);
            font-family: $family2;
        }

    }
}

.dropdown-toggle {

    &:after {
        width: 8px;
        height: 6px;
        border: 0;
        background-image: url('data:image/svg+xml,<svg width="8" height="6" viewBox="0 0 8 6" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0.5 1L4 5L7.5 1" stroke="black" stroke-linecap="round"/></svg>');
        background-position: center;
        background-repeat: no-repeat;
        margin: 0 6px;
        vertical-align: middle;
    }
}