.about-section {
    @media (max-width: 767.98px) {
        text-align: center;
    }

    .video-about {
        position: relative;
        display: inline-block;

        @media (max-width: 575.98px) {
            margin-bottom: 10px;
        }

        img {
            max-width: 100%;
            max-height: 100%;
            border-radius: 26.4px;
        }

        .play-btn {
            display: block;
            width: 140px;
            height: 130px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            background: url(../images/play.svg)no-repeat center;
            border: 0;
            z-index: 10;
        }
    }


    .title__section {
        margin-bottom: 1rem;

        .subhead {
            color: $blue;
        }
    }

    .main-text {
        font-size: 1.4rem;
        margin-bottom: 2rem;
    }

    .btns-action {
        .btn {
            width: auto;
            height: 50px;


        }
    }
}

.clients-section {
    .title__section {
        margin-bottom: 1.6rem;
    }

    ul {


        li {
            @media (max-width: 575.98px) {
                width: 33%;
            }
        }

        img {
            max-width: 100%;
        }
    }
}

.features-kids-section {

    .main-text {
        margin-bottom: 2rem;
    }

    .features-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .feature-item {
            display: inline-flex;
            align-items: center;
            flex: 0 0 50%;
            margin-bottom: 32px;
            color: #101828;
            font-size: 1.25rem;
            font-family: $family2;


            i {
                margin-right: 1.25rem;
            }
        }
    }

    .g-imge {
        position: relative;



        @media (max-width: 767.98px) {

            height: 400px;
            transform: scale(.8);
            width: 400px;
            margin: 0 auto;



        }

        @media (max-width: 575.98px) {

            width: auto;
            height: auto;
            transform: scale(1);


        }

        .s-icon {
            position: absolute;
            box-shadow: 0px 6.112px 4.686px 0px rgba(184, 223, 242, 0.45);
            @include border-radius(5px);

            @media (max-width: 575.98px) {


                display: none !important;



            }
        }

        .img-7 {
            right: 4%;
            top: 11px;
        }

        .img-6 {
            right: 4%;
            top: 185px;
        }

        .img-5 {
            right: 52%;
            top: 265px;
        }

        .img-4 {
            left: 0%;
            top: 100px;
        }

        .img-3 {
            left: 10%;
            top: -35px;
            width: 94px;
            height: 94px;
            @include border-radius(50%);
            border: 1px solid #B8DFF2;
            background: #F0FBFF;
            display: flex;
            align-items: center;
            justify-content: center;

            .slider-movies {
                text-align: center;
            }
        }

        .img-2 {
            left: 38%;
            top: -65px;
        }

        .img-1 {
            z-index: 3;
            // left: 95px;
            left: 17%;
            top: 50px;
            width: 355px;
            height: 220px;
            border-radius: 17.874px;
            border: 1px solid #B8DFF2;
            background: #F0FBFF;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            @media (max-width: 991.98px) {

                width: 250px;



            }

            @media (max-width: 575.98px) {


                display: flex !important;
                position: relative;
                top: auto;
                left: auto;
                margin: 0 auto 15px;



            }

            .slides-title {
                font-family: $family;
                font-size: 1.25rem;
                margin-bottom: .6rem;
            }

            li {
                text-align: center;
            }


        }

        &:hover {
            .s-icon {
                transition: all 1s;
            }

            .img-1 {
                left: 100px;

                top: 45px;

                @media (max-width: 575.98px) {


                    top: auto;
                    left: auto;



                }

            }

            .img-2 {
                top: -90px;
                left: 36%;
            }

            .img-3 {
                left: 8%;
                top: -55px;
            }



            .img-4 {
                left: 2%;
                top: 120px;
            }



            .img-5 {
                right: 50%;
                top: 260px;
            }



            .img-6 {
                right: 6%;
                top: 195px;
            }

            .img-7 {
                right: 2%;
                top: 0px;
            }
        }
    }

}

.features-parents-section {
    .title__section {
        margin-bottom: 1.25rem;
    }

    .main-text {
        font-size: 1.25rem;
        margin-bottom: 3rem;
    }

    .features {
        display: flex;
        justify-content: space-between;

        @media (max-width: 991.98px) {

            flex-wrap: wrap;
        }

        .feature {
            flex: 0 0 24%;

            @media (max-width: 991.98px) {

                flex: 0 0 49%;
            }

            @media (max-width:575.98px) {
                flex: 0 0 300px;
                margin: 0 auto 10px;
            }

            h4 {
                color: $primary;
            }
        }
    }


    .main-provides {
        background-color: #F2F2F2;
        text-align: center;
        margin-bottom: 1rem;
        height: 170px;
        display: flex;
        align-items: center;
        @include border-radius(9px);

        h4 {
            font-family: $family4;
        }

        video {
            @include border-radius(9px);

        }

    }

    .main-description {


        h4 {
            font-family: $family;
        }

        ul {
            padding-left: 1rem;
            list-style: disc;
        }


    }
}

.stats-section {
    .title__section {
        margin-bottom: 1.25rem;

        .subhead {
            color: #4C4C4C;
        }
    }

    .main-text {
        font-size: 1.5rem;
        margin-bottom: 4rem;
    }

    .stats-item {
        @include border-radius(11px);
        border: 1px solid #EEE;
        box-shadow: 0px 3.45px 11.042px 0px rgba(0, 0, 0, 0.04);
        padding: 1.5rem 0;
        width: 16%;
        background: #fff;

        @media (max-width: 991.98px) {

            flex: 0 0 32%;
            margin-bottom: 10px;
        }

        @media (max-width: 575.98px) {
            flex: 0 0 49%;


        }

        .ico-stats {
            margin-bottom: .75rem;
        }
    }

    .desc-stats {
        span {
            display: block;
        }

        .num-stats {
            font-size: 2.3rem;
            font-weight: bold;
        }
    }

    .content__section {
        &.sm-template {
            @include border-radius(1.5rem);
            background: #7BBFF0;
            padding: 3.7rem 5.5rem;

            @media (max-width: 575.98px) {
                padding: 2rem 1rem;


            }

            .stats-item {
                box-shadow: 0px 2.932px 9.383px 0px rgba(0, 0, 0, 0.16);
                @include border-radius(9px);
                padding: 1.25rem 0;


                .ico-stats {
                    height: 63px;
                    margin-bottom: .5rem;

                    img {
                        height: 100%;
                    }
                }
            }

            .desc-stats {
                span {
                    display: block;
                    font-size: .9rem;
                }

                .num-stats {
                    font-size: 2rem;
                }
            }
        }
    }
}

.growth-section {
    .banners-companies {
        display: grid;
        gap: 20px;
        grid-template-columns: repeat(3, auto);
        grid-template-rows: auto auto auto;

        @media (max-width:575.98px) {
            gap: 5px;
        }

        .c-item {
            position: relative;

            .details {
                position: absolute;
                padding: 20px;
                bottom: 50%;
                left: 0;
                width: 100%;
                transform: translate(0, 50%);
                transition: all 2s;

                @media (max-width:575.98px) {
                    padding: 10px;
                }

                .logo {
                    display: inline-block;
                    position: relative;
                    bottom: 50%;
                    left: 50%;
                    transform: translate(-50%, 0);
                    transition: all 2s;

                    @media (max-width:991.98px) {
                        width: 80%;
                    }
                }



                p {
                    font-family: $family;
                    font-size: 1.5rem;
                    color: #fff;
                    height: 0;
                    opacity: 0;
                    transition: opacity 1s;

                    @media (max-width:767.98px) {
                        font-size: 1rem;


                    }


                }

            }
        }

        .company-1 {
            grid-column: 1 / 3;
            grid-row: 1;

            &:hover {
                .details {
                    bottom: 0;
                    transform: none;

                    .logo {
                        width: 300px;

                        display: inline-block;
                        position: relative;
                        bottom: auto;
                        left: 0;
                        transform: none;

                        @media (max-width:991.98px) {
                            width: 70% !important;

                        }
                    }

                    p {

                        opacity: 1;
                        visibility: visible;

                        height: auto;

                    }
                }
            }
        }

        .company-2 {
            grid-column: 3/ 4;
            grid-row: 1 / 3;

            &:hover {
                .details {
                    bottom: 0;
                    transform: none;

                    .logo {
                        width: 300px;

                        display: inline-block;
                        position: relative;
                        bottom: auto;
                        left: 0;
                        transform: none;

                        @media (max-width:991.98px) {
                            width: 70% !important;

                        }

                    }

                    p {

                        opacity: 1;
                        visibility: visible;

                        height: auto;

                    }
                }
            }
        }

        .company-3 {
            grid-column: 1 / 2;
            grid-row: 2;

            &:hover {
                .logo {
                    width: 240px;
                }
            }


        }

        .company-4 {
            grid-column: 2 / 3;
            grid-row: 2;

            &:hover {
                .logo {
                    width: 278px;
                }
            }


        }

        .company-5 {
            grid-column: 1 / 2;
            grid-row: 3;

            &:hover {
                .logo {
                    width: 160px;
                }
            }


        }

        .company-6 {
            grid-column: 2 / 4;
            grid-row: 3;

            &:hover {
                .logo {
                    width: 150px;
                }
            }

        }

        .effect2 {
            &:hover {
                .details {
                    bottom: auto;
                    top: 0;
                    transform: none;

                    .logo {

                        display: inline-block;
                        position: relative;
                        bottom: auto;
                        left: 0;
                        transform: none;

                        @media (max-width:991.98px) {
                            width: 70% !important;

                        }
                    }

                    p {

                        opacity: 1;
                        visibility: visible;

                        height: auto;

                    }
                }
            }
        }
    }
}


.testimonial-section {
    .main-text {
        font-size: 1.25rem;
        margin-bottom: 3rem;
    }

    .comment {
        display: flex;
        align-items: center;
        box-shadow: 0px 0px 2px 2px inset rgba(123, 191, 240, .75);
        border-radius: 42px;
        padding: 3.5rem;

        @media (max-width: 991.98px) {

            padding: 1.5rem;
        }

        @media (max-width: 767.98px) {
            flex-direction: column;

        }

        .card-img {
            @include border-radius(39px);
            border: 1px solid #CCC;
            width: 300px;
            height: 300px;
            flex: 0 0 300px;

            @media (max-width: 767.98px) {
                width: 200px;
                height: 200px;
                flex: 0 0 200px;
                margin-bottom: 10px;

            }

            img {
                border-radius: inherit;
                width: 100%;
            }
        }

        .descripe {
            margin-left: 2.5rem;

            .person-details {
                display: flex;
                align-items: center;
                color: $primary;

                h4 {
                    font-family: $family;
                }

                h5 {
                    font-family: $family2;

                }
            }
        }
    }
}

.cta-wrapper {
    width: 100%;
    height: 500px;
    @include border-radius(23px);
    background: #7BBFF0 url("../images/bg-awards.svg")no-repeat;
    background-size: cover;
    color: #fff;
    position: relative;
    overflow: hidden;

    @media (max-width: 991.98px) {
        height: 330px;



    }

    @media (max-width: 767.98px) {
        flex-direction: column-reverse;
        height: auto;
    }

    .heading {
        font-size: 4rem;
        font-family: $family;
        white-space: nowrap;

        @media (max-width: 1399.98px) {
            font-size: 3rem;
        }

        @media (max-width: 991.98px) {

            font-size: 2rem;



        }
    }

    h2 {
        font-family: $family3;
        margin-bottom: 2.5rem;
    }

    .effect {
        // justify-content: center;
        // transition: all 3s;
        position: relative;

        // transform: translate(0, 0);
        // transition: all 3s;

    }

    // .content {
    //     overflow: hidden;

    .txt {
        position: absolute;

        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: all 3s;

        @media (max-width: 767.98px) {
            position: relative;
            left: auto;
            top: auto;
            transform: none;



        }


        h2,
        p {
            text-align: center;
            white-space: nowrap;
            width: 100%;
            transition: width 3s;
        }


        // h2,
        // p {
        //     display: inline-block;
        //     transform: translateX(-50%);

        //     transition: all 3s;

        // }

        // }
    }

    .download-links {
        display: flex;
        align-items: center;
        transition: all 3s;

        @media (max-width: 575.98px) {
            flex-wrap: wrap;
            justify-content: center;



        }

        a {
            margin: 0 6px;
            width: 170px;

            @media (max-width: 767.98px) {
                width: 130px;
                margin-bottom: 10px;



            }
        }

        .two-btns {
            display: inline-flex;



        }

        .two-btns:first-child {
            transform: translate(0, 0);
            transition: all 3s;

            @media (max-width: 767.98px) {


                transform: none;



            }

        }

        .two-btns:nth-child(2) {
            transform: translate(0, 0);
            transition: all 3s;

            @media (max-width: 767.98px) {


                transform: none;



            }


        }

    }

    .banner-cta {
        position: absolute;
        bottom: 0;
        right: -553px;
        opacity: 0;
        transition: all 3s;

        @media (max-width: 991.98px) {
            width: 365px;



        }

        @media (max-width: 767.98px) {
            position: relative;
            bottom: auto;
            right: auto;
            opacity: 1;


        }
    }

    &:hover {
        .effect {
            // justify-content: flex-start;
            // transition: all 3s;
            // transform: translate(0, 0);
            // transition: all 3s;
        }

        // .content {
        //     transition: all 3s;

        .txt {
            position: absolute;
            // text-align: right;
            left: 0;
            transform: translate(0, -50%);
            transition: all 3s;

            @media (max-width: 767.98px) {
                position: relative;
                transform: none;
                left: auto;


            }

            h2,
            p {
                width: 0;
                transition: width 3s;

                @media (max-width: 767.98px) {
                    width: auto;


                }

            }

            // h2,
            // p {
            //     display: inline-block;
            //     transform: translateX(0);

            //     transition: all 3s;

            // }
        }

        // }

        .download-links {
            // flex-direction: column-reverse;
            // align-items: flex-start;
            transition: all 3s;


            .two-btns:first-child {
                transform: translate(0, 60px);
                transition: all 3s;

                @media (max-width: 767.98px) {
                    transform: none;


                }
            }

            .two-btns:nth-child(2) {

                transform: translate(-360px, 0);
                transition: all 3s;

                @media (max-width: 767.98px) {
                    transform: none;


                }

            }

            a {
                margin-bottom: .8rem;
            }
        }

        .banner-cta {
            right: 0;
            opacity: 1;
            transition: all 3s;


        }

    }
}

.pricing-section {
    .main-text {
        font-size: 1.25rem;
        margin-bottom: 4rem;
    }

    .buying-card {
        margin-bottom: 2rem;

        .card-price {
            border-radius: 25px;
            border: 1px solid #DCDCDC;
            background: #FFF;
            box-shadow: 0px -5px 15.1px 0px rgba(0, 0, 0, 0.08);
            justify-content: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 2rem 1rem;

            @media (max-width: 767.98px) {
                margin-bottom: 10px;

            }

            .icon {
                margin-bottom: 1.563rem;
                border-radius: 50%;
                width: 95px;
                height: 95px;
                overflow: hidden;
            }

            .main-title {
                margin-bottom: 2.6rem;

                h2 {
                    font-size: 1.188rem;
                    color: #000000;
                    margin-bottom: 1rem;
                    font-family: $family4;

                }

                p {
                    font-size: 0.75rem;
                    font-family: $family3;
                    color: #000000;
                }
            }

            .price {
                margin-bottom: 1.563rem;
                font-size: 3.125rem;
                color: $primary;
                font-family: $family;

            }

            .action {


                .btn {
                    color: #ffffff;
                    font-family: $family;
                    line-height: 50px;
                    width: 305px;
                    height: 58px;
                    margin-bottom: 1.563rem;
                    border-radius: 12px;
                    background-color: #3D0429;

                }
            }

            .more {
                font-size: 1.188rem;
                color: $primary;
                font-family: $family;
            }
        }
    }

    .pay {
        .h-pay {
            margin-bottom: 1rem;

            .how-pay {
                font-size: 2.25rem;
                font-family: $family;
            }

            .have-account {
                font-size: 1.188rem;
                color: #4F0F4B;


            }
        }

        .col-pay {
            flex: 0 0 20%;

            @media (max-width: 991.98px) {
                flex: 0 0 33%;
                margin-bottom: 10px;
            }

            @media (max-width: 575.98px) {
                flex: 0 0 50%;
                margin-bottom: 10px;
            }
        }

        .pay-card {
            padding: 1.5rem 1rem;
            border-radius: 20px;

            border: 3px solid rgba(238, 34, 238, 0.18);

            .main-title {
                margin-top: .7rem;
                font-size: 1.3rem;
                font-family: $family;

                @media (max-width: 1399.9px) {
                    font-size: 1.3rem;
                }

                @media (max-width: 575.98px) {
                    font-size: 1.2rem;

                }
            }

            &:hover {
                background: #AA009F;
                border-color: #AA009F;

                .main-title {
                    color: #fff;
                }
            }
        }
    }
}