  .question {
    margin-bottom: 1.125rem;
    @include border-radius(17px);
    box-shadow: 0px 7.195px 19.188px 0px rgba(74, 58, 255, 0.19);

    .title-question {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1.8rem 3rem 1.1rem 3rem;
      cursor: pointer;

      @media (max-width: 575.98px) {
        padding: 1.8rem 2rem 1.1rem 2rem;

      }

      h4 {
        color: #170F49;
        font-family: $family5;
      }

      i {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        @include border-radius(50%);
        box-shadow: 0px 5.996px 19.188px 0px rgba(8, 15, 52, 0.06);

        @media (max-width: 575.98px) {
          width: 30px;
          height: 30px;
          flex: 0 0 30px;
        }
      }


    }

    .answer {
      display: none;
      color: #6F6C90;
      line-height: 2rem;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 0 0 17px 17px;
      padding: 0 4rem 2.25rem 3rem;

      @media (max-width: 575.98px) {
        padding: 0 2rem 2.25rem 2rem;

      }

    }

    &.active {
      i {
        background: #7BBFF0;
        box-shadow: none;
        transform: rotate(90deg);

        svg {
          path[stroke="#88115F"] {
            stroke: #fff;
          }
        }

      }

    }
  }

  .jeelquest {
    h2 {
      text-align: center;
      margin-bottom: 1.5rem;
      font-family: $family;
    }

    .question {
      margin-bottom: 1rem;

      .title-question {
        padding: 1rem 2.8rem;

        h6 {
          color: #170F49;

          @media (max-width: 991.98px) {

            font-family: $family;



          }

        }

        i {
          width: 40px;
          height: 40px;
        }
      }

      &.active {
        i {
          svg {
            path {
              stroke: #fff
            }
          }

        }
      }
    }
  }