html {
    font-size: 16px;



    // `md` applies to small devices (landscape phones, less than 768px)
    @media (max-width: 1199.98px) {
        font-size: 14px;
    }

    // `sm` applies to x-small devices (portrait phones, less than 576px)
    @media (max-width: 575.98px) {
        font-size: 12px;
    }
}

body {
    color: $black;
    cursor: default;
}



img,
.btn {
    max-width: 100%;
}

ul {
    list-style: none;
    padding: 0;
}

textarea {
    resize: none;
}

a {
    text-decoration: none;
}

p {
    margin: 0;
}

.main-text {
    color: $gray;

}

.btn {
    font-family: $family;
}

.btn-primary {
    font-size: 1.25rem;
    background-color: $primary;
    color: #fff;
    @include border-radius(12px);

    &.w-med {
        width: 255px;
        height: 57px;
    }
}

.btn-outlineprimary {
    font-size: 1.25rem;
    color: $primary;
    background: #fff;
    border: 2px solid $primary;
    @include border-radius(12px);
}

h5,
h4 {
    margin: 0;
}

.head {
    font-size: 3rem;
    font-family: $family;

    @media (max-width: 991.98px) {

        font-size: 2rem;



    }
}

.subhead {
    font-size: 1rem;
    font-family: $family;
}

.text-primary {
    color: $primary;
}

.text-blue {
    color: $blue;
}

.text-primarydark {
    color: $primary-dark;
}

.text-blacklight {
    color: $black-light;
}

.mb-section {
    margin-bottom: 2.75rem;

    @media (max-width: 767.98px) {
        margin-bottom: 1.75rem;

    }


}

.py-section {
    padding: 3rem 0;

    @media (max-width: 1199.98px) {
        padding: 2rem 0;

    }
}

.py-page {
    padding: 4rem 0;

    @media (max-width: 1199.98px) {
        padding: 2rem 0;

    }
}

.img-wrapper {
    width: 100%;

    img {
        border-radius: 28px;
    }
}

.template-btns {
    .btn {
        width: 220px;
        height: 57px;
    }

}