$family: "PingARLT-Bold";
$family2: "PingARLT-Medium";
$family3: "PingARLT-Regular";
$family4: "PingARLT-Heavy";
$family5: "IBMPlexSansArabic-Bold";
$family6: "IBMPlexSansArabic-Medium";
$family7: "IBMPlexSansArabic-Regular";
$family8: "IBMPlexSansArabic-SemiBold";


@mixin fontFace($family, $src, $style: normal, $weight: normal) {
  @font-face {
    font-family: $family;
    src: url("#{$src}.eot"); // IE9 compat
    src: url("#{$src}.eot?#iefix") format("embedded-opentype"),
    // IE8 and below
    url("#{$src}.woff") format("woff"),
    // standards
    url("#{$src}.ttf") format("truetype"),
    // Safari, Android, iOS
    url("#{$src}.svg##{$family}") format("svg"); // legacy iOS
    font-style: $style;
    font-weight: $weight;
  }
}

@include fontFace($family, "../fonts/PingARLT-Bold");
@include fontFace($family2, "../fonts/PingARLT-Medium");
@include fontFace($family3, "../fonts/PingARLT-Regular");
@include fontFace($family4, "../fonts/PingARLT-Heavy");
@include fontFace($family5, "../fonts/IBMPlexSansArabic-Bold");
@include fontFace($family6, "../fonts/IBMPlexSansArabic-Medium");
@include fontFace($family7, "../fonts/IBMPlexSansArabic-Regular");
@include fontFace($family8, "../fonts/IBMPlexSansArabic-SemiBold");

body {
  font-family: $family7;
}

@mixin border-top-left-radius($value) {
  -webkit-border-top-left-radius: $value;
  -moz-border-top-left-radius: $value;
  -ms-border-top-left-radius: $value;
  border-top-left-radius: $value;
}

@mixin border-top-right-radius($value) {
  -webkit-border-top-right-radius: $value;
  -moz-border-top-right-radius: $value;
  -ms-border-top-right-radius: $value;
  border-top-right-radius: $value;
}

@mixin border-bottom-left-radius($value) {
  -webkit-border-bottom-left-radius: $value;
  -moz-border-bottom-left-radius: $value;
  -ms-border-bottom-left-radius: $value;
  border-bottom-left-radius: $value;
}

@mixin border-bottom-right-radius($value) {
  -webkit-border-bottom-right-radius: $value;
  -moz-border-bottom-right-radius: $value;
  -ms-border-bottom-right-radius: $value;
  border-bottom-right-radius: $value;
}

@mixin border-radius($value) {
  -webkit-border-radius: $value;
  -moz-border-radius: $value;
  -ms-border-radius: $value;
  border-radius: $value;
}