.downloadapp {
    overflow: hidden;
    position: relative;

    @media (max-width: 1199.98px) {
        text-align: center;
    }

    .text-side {
        padding: 0 3rem;

        @media (max-width: 991.98px) {
            padding: 0;
        }

        .heading {
            color: #60265C;
            font-size: 4rem;
            margin-bottom: .9rem;
            font-family: $family;

            @media (max-width: 1399.98px) {
                font-size: 2.5rem;
            }
        }

        h2 {
            font-family: $family3;
            margin-bottom: 2.5rem;
        }
    }


    .apps {
        position: relative;

        .banner-side {
            position: relative;

            &:before {
                content: " ";
                position: absolute;
                z-index: -1;
                right: 0;
                bottom: 25px;
                background: #7BBFF0;
                @include border-radius(32px);
                width: 50vw;
                max-width: 800px;
                height: 70%;

                @media (max-width: 767.98px) {
                    width: 100vw;
                }
            }
        }

        &.mobile-apps {
            .banner-side {

                &:before {

                    left: 10%;

                    @media (max-width: 767.98px) {
                        left: 0;
                    }

                }
            }
        }
    }


}