@keyframes fadeInRightSlide {
    0% {
        -webkit-transform: translateX(-100px);
        transform: translateX(-100px);
    }

    100% {
        -webkit-transform: none;
        transform: none;
    }
}

.fadeInRightSlide {
    -webkit-animation-name: fadeInRightSlide;
    animation-name: fadeInRightSlide;
}

@keyframes fadeInLeftSlide {
    0% {
        -webkit-transform: translateX(100px);
        transform: translateX(100px);
    }

    100% {
        -webkit-transform: none;
        transform: none;
    }
}

.fadeInLeftSlide {
    -webkit-animation-name: fadeInLeftSlide;
    animation-name: fadeInLeftSlide;
}

@keyframes fadeImage {
    0% {
        background-image: url(../images/decore2.svg);

    }

    100% {
        background-image: url(../images/decore1.svg);

    }
}

.fadeImage {
    -webkit-animation-name: fadeImage;
    animation-name: fadeImage;
}



.slide {
    position: relative;

    img {
        width: 100%;

        @media (max-width: 575.98px) {
            width: auto;
            max-width: none;
            height: 300px;
        }
    }

    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        width: auto;
        height: 100%;

        img {
            min-height: 100%;
        }

    }

    .wrap-slide {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: 100%;
        color: #fff;
        z-index: 20;



        .left-section {
            width: 45%;

            @media (max-width: 991.98px) {
                width: 100%;
                text-align: center;

            }

            .head {
                margin-bottom: .75rem;
            }

            p {
                font-size: 1.25rem;
                margin-bottom: 2rem;
            }

            .btn {
                width: 245px;
                height: 55px;
                margin-right: 10px;

                @media (max-width: 991.98px) {
                    width: 145px;
                    height: 35px;

                }
            }
        }
    }
}


.slick-slide {
    &:nth-child(1) {
        &.slick-active img {
            animation-name: fadeIn;
            animation-duration: 1s;
            opacity: 1;
        }

    }

    &:nth-child(2) {
        &.slick-active img {
            animation-name: fadeIn;

            animation-duration: 1s;
            opacity: 1;
        }

    }

    &:nth-child(3) {

        &.slick-active img {
            animation-name: fadeInRightSlide;
            animation-duration: 1s;
            opacity: 1;
        }


    }
}

.banner_slider {


    .slide {
        height: 650px;
        overflow: hidden;

        @media (max-width: 1199.98px) {

            height: auto;

        }
    }


}

.home_banner {
    position: relative;
}



.slick-dots {
    bottom: 8px;

    li {
        width: 10px;

        button {

            background: rgba(255, 255, 255, 0.70);
            opacity: .7;
            width: 8px;
            height: 8px;
            padding: 0;
            z-index: 100000000;
            position: relative;
            border-radius: 50%;

            &:before {
                display: none;

            }
        }

        &.slick-active {
            width: 40px;
            transition: all 1s;

            button {
                width: 40px;
                opacity: 1;
                border-radius: 3px;
                transition: width 1s;

            }

        }

    }
}