.contact-section {
    .title__section {
        h4 {
            color: #323232;
            font-family: $family6;
        }
    }

    .social-links {
        li {
            margin: 0 7px;

            a {
                display: block;

                &:hover {
                    path[stroke="#88115F"] {
                        fill: $primary;
                        stroke: $primary;
                        opacity: 1;
                    }

                    path[fill="#88115F"] {
                        fill: #fff
                    }

                    path[fill="#88115F"][stroke="#88115F"] {
                        fill: #fff;
                        stroke: #fff;

                    }

                }
            }
        }
    }

    .contact-info {
        height: 645px;
        @include border-radius(20px);
        background: #71B3E1 url(../images/bg-contactinfo.svg)no-repeat;
        background-size: cover;
        color: #fff;

        h2 {
            font-family: $family4;
        }

        ul {
            li {
                margin: 1.5rem 0;

                &:hover {
                    svg {
                        path[fill="white"] {
                            fill: $primary;

                        }

                        path[fill="#88115F"][stroke="#88115F"] {
                            fill: #fff;
                            stroke: #fff;

                        }

                        path[fill="#88115F"] {
                            fill: #fff;

                        }
                    }
                }

                span {
                    margin: 0 1rem;
                }
            }
        }
    }
}

.form-contact {
    .form-label {
        font-size: .87rem;
        font-family: $family2;

    }

    input {
        height: 48px;
    }

    .form-control {
        @include border-radius(8px);
        border: 1px solid #BBBBBB;


        &::placeholder {
            font-size: .75rem;
            color: #BEBEBE;
        }

    }

    .form-select {
        @extend .form-control;
        height: 48px;
        background-image: url('data:image/svg+xml,<svg width="11" height="8" viewBox="0 0 11 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M6.34881 7.29005L10.6382 1.5709C11.0611 1.00704 10.6587 0.202392 9.95392 0.202392L1.37519 0.202392C0.670366 0.202392 0.26804 1.00704 0.690933 1.5709L4.9803 7.29005C5.32242 7.74622 6.00668 7.74622 6.34881 7.29005Z" fill="%23402A22"/></svg>');
        background-position: right 10px center;
        background-size: 12px;


        &:required:invalid {
            font-size: .75rem;
            color: #BEBEBE;
        }

        option[value=""][disabled] {
            display: none;
        }

        option {
            font-size: 1rem;

            color: black;
        }


    }

    .btn {
        @include border-radius(12px);
        height: 57px;


    }
}

.page-contact {
    @extend .contact-section;

    .contact-info {
        width: auto;
        padding: 0 3.2rem;

        @media (max-width: 991.98px) {

            padding: 0 2rem;
            width: 100%;




        }

        ul {
            li {
                span {
                    font-size: 1.6rem;

                    @media (max-width: 991.98px) {

                        font-size: 1.2rem;

                    }
                }
            }
        }
    }
}

.tel {
    @include border-radius(8px);
    border: 1px solid #bbb;

    .form-control {
        border: 0;
        @include border-radius(8px !important);

        &:focus {
            box-shadow: none;
        }
    }

    .btn {
        height: auto;
        color: #202236;
        font-size: .9rem;
        border: 0;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            left: 0;
            width: 1px;
            height: 21px;
            background: #BEBFC2;
            top: 50%;
            margin-top: -10px;
        }
    }

    .dropdown-menu {
        min-width: auto;
    }
}