.header-section {
    position: relative;

    .banner {
        min-height: 400px;




        @media (max-width: 767.98px) {
            min-height: 260px;
            overflow: hidden;


        }

        img {
            width: 100%;

            @media (max-width: 575.98px) {
                width: auto;
                max-width: none;
                height: 260px;


            }

        }
    }

    .main-title {
        text-align: center;
        z-index: 3;
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);

        @media (max-width:1199.98px) {
            width: 100%;
            padding: 0 1rem;

        }

        @media (max-width: 767.98px) {
            top: 15px;
            right: 0;
            transform: none;

        }

        .head {
            color: #fff;
            margin-bottom: 0.75rem;
        }

        p {
            font-size: 1.25rem;
            color: #fff;
            margin-bottom: 2rem;
        }

        .action {
            .btn {
                width: 254px;
                height: 57px;

                @media (max-width: 575.98px) {
                    width: 145px;
                    height: 35px;

                }
            }

            .btn-primary {

                border: none;
                margin-right: 1rem;


            }


        }
    }
}

.about-overview-section {
    .title__section {
        .head {
            margin-bottom: 1.25rem;
        }


    }

    .main-text {
        font-size: 1.5rem;
        color: $gray;
        margin: 1.25rem 0;
    }

    .statistics {
        li {
            display: flex;
            margin-bottom: .9rem;

            .num {
                margin-right: 0.75rem;
                font-size: 1.875rem;
                color: #53B0F3;
                font-family: $family;
            }

            p {
                font-size: 1.5rem;
                color: #757575;

                .proportion {
                    color: $primary;
                    text-decoration: underline;
                    font-family: $family6;


                }
            }
        }
    }

    .charts {
        display: flex;
        justify-content: space-between;

        @media (max-width: 767.98px) {
            margin-bottom: 10px;
            justify-content: center;
        }

        .chart {
            @include border-radius(14px);
            border: 1px solid #90CBF5;
            background: #fff;
            box-shadow: 0px 2.901px 9.282px 0px rgba(0, 0, 0, 0.16);
            padding: 1.3rem;

            @media (max-width: 767.98px) {
                margin: 0 5px;
            }
        }


        .analyst {
            p {
                display: flex;
                justify-content: space-between;
                align-items: center;
                color: #767676;
                font-size: .9rem;
                font-family: $family;
                margin-top: .8rem;

                .num {
                    color: #404040;
                    font-weight: bold;

                }
            }
        }
    }



}

.feature-section {
    .head {
        margin-bottom: 1.25rem;
    }

    p {
        font-size: 1.5rem;
        color: $gray;
    }

    .title__section {

        @media (max-width: 991.98px) {
            text-align: center;

        }
    }

    .main-banner {
        position: relative;

        @media (max-width: 991.98px) {
            margin-bottom: 40px;

        }

        .head {
            max-width: 70%;
            font-size: 2.2rem;
        }

        .mobile-banner {
            position: absolute;
            z-index: 3;
            top: 65%;
            right: 20%;
            transform: translate(50%, -50%);
        }


    }
}

.features-kids-section {
    .features-list {


        .item {
            flex: 0 0 32%;
            margin-bottom: 2rem;

            @media (max-width: 767.98px) {
                flex: 0 0 48%;


            }

            @media (max-width: 575.98px) {
                flex: 0 0 300px;
                margin: 5px auto;


            }

            &:nth-last-child(-n+3) {
                margin-bottom: 0;
            }

        }
    }

    .main-card {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 27px 23px;
        border-radius: 16px;
        border: 1px solid #F2F2F2;
        background: #FFF;
        box-shadow: 0px 0.98px 2.941px 0px rgba(0, 0, 0, 0.16);
        outline: 3px solid transparent;
        transition: all .5s;

        .icon-stats {
            width: 100%;
            height: 235px;
            border-radius: 18px;
            background-color: #F2F2F2;
            margin-bottom: 1rem;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                transition: all .5s;
            }




        }

        &:hover {
            outline: 3px solid #7BBFF0;

            .icon-stats {
                img {
                    transform: scale(1.1);

                }
            }
        }

        .main-title {
            font-size: 2.375rem;
            color: $primary;
            font-family: $family;

            @media (max-width: 991.98px) {
                font-size: 1.375rem;




            }
        }
    }
}

.feature-section-parents {
    margin-bottom: 3rem;

    .main-title {
        width: 575px;
        height: 335px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #F2F2F2;
        border-radius: 16px;
        font-size: 3.063rem;
        color: $primary;
        max-width: 100%;

        video {
            border-radius: 16px;


        }

        @media (max-width: 575.98px) {
            width: 100%;



        }
    }

    .details {
        width: 575px;
        height: 335px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 100%;

        @media (max-width: 575.98px) {
            width: 100%;
            height: auto;
            margin: 10px 0 25px;


        }

        h1 {
            color: $primary;
            font-family: $family;
        }

        ul {
            list-style: disc;
            padding-left: 1.5rem;

            li {
                font-size: 1.5rem;
                color: $gray;
            }
        }
    }
}

.awards-section {
    .title__section {
        margin-bottom: 1.6rem;
    }


    .award-item {
        border: 1px solid #DCDCDC;
        padding: 14px 22px;
        @include border-radius(7px);
        margin: 0 8px;
        width: 40%;

        .logo-award {
            width: 254px;
            height: 165px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            background: #F2F2F2;
            @include border-radius(6px);
            margin-right: 18px;

            @media (max-width: 767.98px) {
                width: 205px;
                height: 100px;

            }

        }

        .desc-awrad {
            h4 {
                font-family: $family;
            }

            p {
                font-family: $family2;
            }
        }
    }
}

.ecommerce-product {
    .title__section {
        padding: 0 2rem;

        @media (max-width: 767.98px) {

            padding: 0;



        }

        @media (max-width: 575.98px) {

            text-align: center;



        }
    }

    .main-banner {
        overflow: hidden;
        display: inline-block;

        @media (max-width: 575.98px) {

            width: 100%;
            text-align: center;



        }

        img {
            @include border-radius(20px);

            @media (max-width: 575.98px) {

                width: 250px;
                margin: 0 auto 10px;



            }
        }
    }

    .social-links {
        @extend .contact-section;



        ul {
            display: flex;
            margin-bottom: 1.75rem;

            @media (max-width: 575.98px) {

                justify-content: center;



            }

            li {
                margin-right: 0.813rem;
            }
        }
    }

    .main-text {
        font-size: 1.5rem;
        color: #323232;
        font-family: $family6;
    }

    .option {
        ul {
            list-style-image: url('../images/marker.svg');
            padding-left: 2rem;

            @media (max-width: 575.98px) {

                text-align: center;
                display: inline-block;



            }

            li {
                font-size: 1rem;
                color: #0D0D0D;
                margin-bottom: 1.5rem;
                padding-left: 1rem;
            }
        }
    }

    .more {
        text-align: center;

        .btn {
            width: 558px;
            height: 57px;
            border-radius: 12px;
        }
    }
}


.conditions {
    .head {
        font-size: 1.6rem;
        color: #402A22;
        margin-bottom: 20px;

    }

    .sub-head {
        font-size: 1.5rem;
        color: #402A22;
        margin-bottom: 15px;
        font-weight: 700;
    }



    ul {
        list-style: disc !important;
        color: rgba(54, 54, 54, .6);
        margin-bottom: 20px;

        li {
            list-style: disc !important;

        }
    }

}