footer {
    background: #151B28;
    color: #fff;
    padding: 4rem 0;

    .bottom-part {
        margin-top: 4rem;
    }

    .company-name {
        margin-bottom: .9rem;
    }

    .pages-links {
        list-style: disc;
        margin-left: 2rem;


        a {
            color: #fff;
            text-decoration: underline;
        }
    }

    .social-links {
        li {
            margin: 0 5px;
        }
    }

    ul {
        li {
            margin-bottom: .7rem;
        }
    }

    .copyright {
        color: #A5ACBA;
        font-family: $family3;
        direction: ltr;
    }
}